@import '_colors.scss';

.erreur_contents_content {
    display: flex;
    flex-direction: column;
    align-items: center;

    &_title {
        margin-top: 100px;
        font-size: 250px;
        margin-bottom: 3px;
    }

    &_subtitle {
        margin-top: 10px;
        margin-bottom: 90px;
    }

    &_button {
        margin-bottom: 145px;
    }
}

@media(max-width: 768px) {
    .erreur_contents_content {
        &_title {
            font-size: 160px;
        }

        &_subtitle {
            padding: 10px;
            font-size: 20px;
            text-align: center;
        }
        
        &_link {
            margin-top: 80px;
        }
    }
}